<template>
  <div id="app">
    <loading v-show="this.$root.bLoading"/>
    <Header v-show="this.$root.bNav"/>
    <router-view></router-view>
    <Footer v-show="this.$root.bFoot"/>
  </div>
</template>
<script>
  import Home from '../pages/home.vue'
  import Header from './header.vue'
  import Footer from '@/layouts/footer.vue'
  import loading from '@/components/loading.vue'
  
  // import $ from 'jquery';
  // import Swipe from '../assets/js/swipe.js'// npm 
  // let Swipe = require('../assets/js/swipe.js')// npm 
  
  export default{
    name:'App',
    components:{
      Home,Footer,Header,loading
    },
    mounted(){
      
    },
    
    //||
    watch:{
      $route:{
        handler(nextValue,PrevValue){
          console.log('path',nextValue.path);
          
          let path = nextValue.path;//  /follow
          //
          
          if(/login|reg|detail/.test(path)){
            this.$root.bNav=this.$root.bFoot=false;
          }
          if(/home|follow|column/.test(path)){
            this.$root.bNav=this.$root.bFoot=true;
          }
          if(path.includes('user')){
            this.$root.bNav=false;
            this.$root.bFoot=true;
          }
        },
        immediate:true
      }
    },
    
  }
</script>
